import React, { Component } from 'react'
import Banner1Image from '../../assets/images/banner1.png';
import TextBoxClass from '../../shared/components/textbox/index';

class MainSection extends Component 
{
    render () {
        var alinkcolor={color:"#008489"}
        return (
            <div className="main-search-container dark-overlay">
            <div className="main-search-inner">

                <div className="container">
                    <div className="row">
                        <div className="col-md-7">
                            <h2 style={{marginTop:"21%"}}><strong>iArtist</strong> will <strong>go live</strong> on <br/> 1st Feb 2018</h2>
                            <p style={{color:"#fff"}}>
                                Curruntly, iArtist is in under construction. we will give you full access on 1st Feb 2018, Now you can register your account. <br/>If you are elighble then we will approve your artist registration request.
                            </p>
                        </div>
                        <div className="col-md-5">
                         
                        <div id='frmReg' hidden ={this.props.RegFrom}>
                            <div className="form-content" style={{background:'#FFF',padding:'25px',paddingbottom:'12px',margintop:'5%'}}>
                                <form id="form_rest">

                                    <div className="row">
                                     <div className="col-md-6">
                                        <TextBoxClass/>
                                            <span id="msg_fname_error" className="error-msg red"></span>
                                        </div> 
                                        <div className="col-md-6">
                                            <div className="form-group form-row">
                                            <label>
                                                <input id="txt_lname" type="name" placeholder="Last Name" name="txt_lname" autoComplete="off" className="form-control"  maxLength="70"/>
                                                <span id="msg_lname_error" className="error-msg red"></span>
                                                </label>
                                            </div>
                                        </div>
                                    </div>
                          
                                    <div className="row">
                                        <div className="col-md-12">
                                            <input id="txt_phone" type="text" placeholder="Phone" autoComplete="off" className="form-control" maxLength="16" />
                                            <span id="msg_phone_error" className="error-msg red"></span>

                                        </div>
                                    </div>

                                    <div className="row">
                                        <div className="col-md-12">
                                            <input id="txt_email" type="text" placeholder="Email Address" autoComplete="off" className="form-control" maxLength="75"/>
                                            <span id="msg_email_error" className="error-msg red"></span>
                                        </div>
                                    </div>

                                    <div className="row">
                                        <div className="col-md-12">
                                            <input id="txt_password" type="password" placeholder="Enter Password" autoComplete="off" className="form-control" maxLength="20"/>
                                            <span id="msg_password_error" className="error-msg red"></span>
                                        </div>
                                    </div>

                                    <div className="row">
                                        <div className="col-md-12 text-center">
                                            <div className="form-group login-submit">
                                              <button type="button" className="button border fw margin-top-10 margin-bottom-15 btn-submit" name="get-started" value="Get Started">Get Started</button>
                                              <br/>
                                             <span>Alredy have an iArist Account ?
                                                <a style={alinkcolor} onClick= {this.props.clickEventLogin}><b><u> Login </u></b> </a>
                                                </span> 
                                            </div>
                                        </div>
                                    </div>

                                </form>
                            </div>
                            </div>

                            <div id='frmLogin' hidden ={this.props.LoginFrom}>
                            <div className="form-content" style={{background:'#FFF',padding:'25px',paddingbottom:'12px',marginTop:'24%'}}>
                        <form id='frmLogin'>
                        <div className="row">
                        <div className="col-md-12 text-center margin-bottom-30">
                        <h3>Log in to iArtist</h3>
                        </div>
                        </div>

                            <div className="row">
                            <div className="col-md-12">
                            
                            <input id="txt_email" type="text" placeholder="Email Address" autoComplete="off" className="form-control" maxLength="75"/>
                            <span id="msg_email_error" className="error-msg red"></span>
                            </div>
                            </div>

                            <div className="row">
                            <div className="col-md-12">
                            <input id="txt_password" type="password" placeholder="Enter Password" autoComplete="off" className="form-control" maxLength="20"/>
                            <span id="msg_password_error" className="error-msg red"></span>
                            </div>
                            </div>

                            <div className="row">
                            <div className="col-md-12 text-center">
                            <div className="form-group login-submit">
                            <button type="button" className="button border fw margin-top-10 margin-bottom-15 btn-submit" name="get-started" value="Get Started">Login</button>
                            <br/>
                            <span>Dont have an account ?<a style={alinkcolor} onClick={this.props.clickEventRegister}> <b><u> Sign up </u></b></a></span> 
                            </div>
                            </div>
                            </div>


                        </form>
                        </div>
                        </div>
                           
                        </div>
                    </div>

                </div>

            </div>

            <div className="video-container">
                <video poster={Banner1Image} loop="" autoPlay="" muted="">
			<source src="" type="video/mp4"/>
		</video>
            </div>
         

        </div>
        )
    }
}
MainSection.defaultProps=
{
    LoginFrom:true,
    RegFrom:false

}


export default MainSection


