import React, { Component } from 'react'
 class SucessAlertMessage extends Component {
     render () {
         return (
<div className="notification success closeable" hidden={this.props.sucessMessageVisible}>
				<p>{this.props.sucessMessageText}</p>
				<a className="close"></a>
			</div>
         )
     }
 }
 SucessAlertMessage.defaultProps=
 {
    sucessMessageText:"",
    sucessMessageVisible:true
 }
 
 export default SucessAlertMessage;
    



