import React, { Component } from 'react'
import Logo1 from '../../../assets/images/logo1.png';

class Footer extends Component {
    render () {
        return (
            <div id="footer">
            
              <div className="container">
                  <div className="row">
                      <div className="col-md-5 col-sm-6">
                          <img className="footer-logo" src={Logo1} alt=""/>
                          <br/>
                          <br/>
                          <p><span style={{fontsize:'19px',fontweight:'300'}}>We love the idea of having a future where we can express and inspire ourselves in our homes or business. No more generic paintings or wall papers!</span></p>
                      </div>
  
                      <div className="col-md-4 col-sm-6 ">
                          <h4>Helpful Links</h4>
                          <ul className="footer-links">
                              <li><a href="">Login</a></li>
                              <li><a href="">Sign Up</a></li>
                              <li><a href="">My Account</a></li>
                           
                              <li><a href="">Privacy Policy</a></li>
                          </ul>
  
                          <ul className="footer-links">
                              <li><a href="">FAQ</a></li>
                            
                              <li><a href="">How It Works</a></li>
                              <li><a href="">Contact</a></li>
                          </ul>
                          <div className="clearfix"></div>
                      </div>
  
                      <div className="col-md-3  col-sm-12">
                          <h4>Contact Us</h4>
                          <div className="text-widget">
                              <span>12345 Little Lonsdale St, Melbourne</span>
                              <br/> Phone: <span>(123) 123-456 </span>
                              <br/> E-Mail:
                              <span> <a href="">info@iartist.co.uk</a> </span>
                              <br/>
                          </div>
  
                          <ul className="social-icons margin-top-20">
                              <li><a className="facebook" href=""><i className="icon-facebook"></i></a></li>
                              <li><a className="twitter" href=""><i className="icon-twitter"></i></a></li>
                              <li><a className="gplus" href=""><i className="icon-gplus"></i></a></li>
                              <li><a className="vimeo" href=""><i className="icon-vimeo"></i></a></li>
                          </ul>
  
                      </div>
  
                  </div>
  
                 
                  <div className="row">
                      <div className="col-md-12">
                          <div className="copyrights"> 2017 iArtist. All Rights Reserved.</div>
                      </div>
                  </div>
  
              </div>
  
          </div>
        )
    }
}

export default Footer


