import React, { Component } from 'react';
import './App.css';
import SideBarMenuClass from '../src/shared/components/sidebarmenu';
import HeaderMenuClass from '../src/shared/components/headermenu/index';
class App extends Component {
  render() {
    return (
      <div id="wrapper">
              <HeaderMenuClass/>
            <div className="clearfix"></div>
            <div id="dashboard">
 <a href="" className="dashboard-responsive-nav-trigger"><i className="fa fa-reorder"></i> Dashboard Navigation</a>
                <SideBarMenuClass/>
                <div className="dashboard-content">
                {this.props.children}
                </div>
            </div>
            </div>
    );
  }
}

export default App;



