import React, { Component } from 'react'
import Logo from '../../../assets/images/logo1.png';
import Logo2 from '../../../assets/images/logo2.png';
import UserIcon from '../../../assets/images/dashboard-avatar.jpg';
//import {Link, IndexLink} from 'react-router';
import {Link} from 'react-router';
import './style.css';

class HeaderMenu extends Component {
    render () {
        return (
            <header id="header-container" className="fixed fullwidth dashboard">
            <div id="header" className="not-sticky">
                <div className="container">
                    <div className="left-side">
                        <div id="logo">
                <a ><img src={Logo} alt=""/></a>
            <a  className="dashboard-logo"><img src={Logo2} alt=""/></a>
                    </div>
                        <div className="mmenu-trigger">
                            <button className="hamburger hamburger--collapse" type="button">
                                <span className="hamburger-box">
                                    <span className="hamburger-inner"></span>
                                </span>
                            </button>
                        </div>
        
                        
                        
                        <div className="clearfix"></div>
                    
                        
                    </div>
                
        
                    
                    <div className="right-side">
                
                        <div className="header-widget">
                            
                            
                            <div className="user-menu">
                                <div className="user-name"><span>
                    <img src={UserIcon} alt=""/></span>Welcome Jhon</div>
                                <ul>
                                {/* <Link to="/dashboard">
                <li><span className="headerlink"><i className="sl sl-icon-settings"></i> Dashboard</span></li>
                </Link> */}
                                    {/* <li><a href="dashboard-messages.html"><i className="sl sl-icon-envelope-open"></i> Messages</a></li> */}
                                    <Link to="/changepassword">
                                    <li><span className="headerlink"><i className="fa sl sl-icon-settings"></i> Edit Password</span></li>
                                    </Link>
                                    <Link to="/">
                                    <li><span className="headerlink"><i className="sl sl-icon-power"></i> Logout</span></li>
                                    </Link>

                                </ul>
                            </div>
                        </div>
                        
                    </div>
                    
        
                </div>
            </div>
            
        
        </header>
        )
    }
}

export default HeaderMenu


