import React from 'react';
import {Route, IndexRoute, Redirect} from 'react-router';
import App from './App';
import LadingPage from './views/ladingpage';


import Dashboard from './views/dashboard';
import OrderPage from './views/order';

import MyServicessPage from './views/myservicess';
import AddServicePage from './views/addservices';

import ChangePasswordPage from './views/changepassword';
import MyProfilePage from './views/myprofile';

import Page404 from './views/pages/404';





export default (
    <Route>
           <Route component={LadingPage} exact path='/'/>
        <Route component={App} path='/'>
            <IndexRoute component={Dashboard}/>
            <Route path='dashboard' component={Dashboard}/>
            <Route path='order' component={OrderPage}/>
            <Route path='myservices' component={MyServicessPage}/>
            <Route path='services' component={AddServicePage}/>
            <Route path='myprofile' component={MyProfilePage}/>
            <Route path='changepassword' component={ChangePasswordPage}/>
            
        </Route>
        <Route component={Page404} path='404'/>
        <Redirect from="*" to="404"/>
    </Route>
);



