import React, { Component } from 'react'
import HowItWorks1 from '../../assets/images/how_it_works1.jpg';
import HowItWorks2 from '../../assets/images/how_it_works2.jpg';
import HowItWorks3 from '../../assets/images/how_it_works3.jpg';

class Section2 extends Component {
    render () {
        return (
            
<section className="fullwidth margin-top-30 padding-top-30 padding-bottom-30" data-background-color="#f8f8f8" style={{background:'rgb(248, 248, 248)',fontsize:'19px',fontweight:'300'}}>
<div className="container">
    <div className="row">
        <div className="col-md-12">
            <div className="demo">
                <h1><b>How to be an iArtist host</b></h1>
            </div>
            <div className="row">
                <div className="col-md-6">
                    <div className="numbered color filled">
                        <h3><span style={{backgroundcolor:'#f91942',borderradius:'25px',padding:'9px 15px',color:'#FFF',fontsize:'16px'}}>1</span>&nbsp;&nbsp;<b> Create your listing</b></h3>
                    </div>
                    <div style={{marginleft:'54px'}}>
                        <span>Its free and easy to create a listing on iArtist. Describe your space, how many guests you can accommodate, and add photos and details.</span>
                        <div style={{marginbottom:'16px'}}>
                            <div className="">
                                <span>Our pricing tool can recommend competitive rates, but what you charge is always up to you.</span>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="col-md-6">
                
                    <img src={HowItWorks1} className="item-image" alt='HowItWorks1'>
                 
                    </img>
                </div>
            </div>
            <div className="clearfix margin-top-30 padding-top-30 padding-bottom-30"></div>
            <div className="row">
                <div className="col-md-6">
                 
                    <img src={HowItWorks2} className="item-image" alt='HowItWorks2'></img>
                </div>
                <div className="col-md-6">
                    <div className="numbered color filled">
                        <h3><span style={{backgroundcolor:'#f91942',borderradius:'25px',padding:'9px 15px',color:'#FFF',fontsize:'16px'}}>2</span>&nbsp;&nbsp;<b> Welcome guests</b></h3>

                    </div>
                    <div style={{marginleft:'54px'}}>
                        <span>Get to know guests before arrival by messaging them on our platform.</span>
                        <div style={{marginbottom:'16px'}}>
                            <div className="">
                                <span>Most hosts clean the spaces guests can use, and provide essentials like clean sheets, towels, and toilet paper.</span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="clearfix margin-top-30 padding-top-30 padding-bottom-30"></div>
            <div className="row">
                <div className="col-md-6">
                    <div className="numbered color filled">
                        <h3><span style={{backgroundcolor:'#f91942',borderradius:'25px',padding:'9px 15px',color:'#FFF',fontsize:'16px'}}>3</span>&nbsp;&nbsp;<b> Get paid</b></h3>

                    </div>
                    <div style={{marginleft:'54px'}}>
                        <span>iArtist's secure payment system means you never have to deal with money directly.<br/>Guests are charged before arrival, and you are paid automatically after check in, minus a 3% service fee.<br/>You can be paid via PayPal, direct deposit, or international money wire, amongst other ways.</span>
                    </div>
                </div>
                <div className="col-md-6">
                   
                <img src={HowItWorks3} className="item-image" alt='HowItWorks3'>
                    </img>
                </div>
            </div>
        </div>
    </div>
</div>
</section>
        )
    }
}

export default Section2


