import React, { Component } from 'react'

class TextBox extends Component {
    render () {
        return (
             <input id="txt_fname" type="name" placeholder="First Name" name="txt_fname" autoComplete="off" className="form-control"  maxLength="70"/>
        )
    }
}

export default TextBox


