import React, { Component } from 'react'
 class DangerAlertMessage extends Component {
     render () {
         return (

<div className="notification  error closeable" hidden={this.props.dangerMessageVisible}>
				<p>{this.props.dangerMessageText}</p>
				<a className="close" ></a>
			</div>

         )
     }
 }
 DangerAlertMessage.defaultProps=
 {
    dangerMessageText:"",
    dangerMessageVisible:true
 }
 
 export default DangerAlertMessage;
    



