import React, { Component } from 'react'
import './style.css';
import NavBarClass from '../../shared/components/ladingnavbar/index';
import FooterClass from '../../shared/components/ladingfooter/index';
import FaqClass from './faq';
import Section2 from './section2';
import Section1 from './section1';
import FooterBannerClass from './footerbanner';
import MainSectionClass from './mainsection';


class LadingPage extends Component 
{
    constructor(props) 
    {
        super(props);
        this.state = {
            showLoginFrom:true,
            showRegFrom:false
          }
      
        this.showlogin=this.showlogin.bind(this);
        this.showRegister=this.showRegister.bind(this);
    }

    showlogin = (e) => {
        this.setState({
            showLoginFrom:false,
            showRegFrom:true
        });
    }
    showRegister = (e) => 
    {
        this.setState({
            showRegFrom:false,
            showLoginFrom:true
        });
    }

    render () 
    {
        return (
            <div>
        <div id="wrapper">

         <NavBarClass/>

        <div className="clearfix"></div>

        <MainSectionClass LoginFrom={this.state.showLoginFrom} RegFrom={this.state.showRegFrom}  clickEventRegister={this.showRegister} clickEventLogin={this.showlogin} />

        <Section1/>

        <Section2/>

        <FaqClass/>

        <FooterBannerClass/>

        <FooterClass/>

        <div id="backtotop">
            <a href="top"></a>
        </div>

            </div>
            </div>
      
        )
    }
}
export default LadingPage


